.complaint-modal {
    display:none;
    position: absolute;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    overflow: hidden;
    color:#7CEEFF;
    background-color: rgba(0,0,0, 0.7);
    font-family: 'Monserrat', sans-serif;
    align-items: center;
    justify-content: center;

    .complaint-modal-wrapper {
        background-color: black;
        position: relative;
        display: grid;
        width: 540px;
        grid-template-columns: 1fr;
        grid-template-rows: max-content auto max-content;
        padding: 20px;
        border-radius:30px;

        .complaint-modal__list {
            height: 100%;
            overflow-y: auto;
            margin-left: 20px;
        }
    }
    &__title {
        text-align:center;
        font-family: 'Monserrat', sans-serif;
        font-size: 32px;                   
        font-weight: 300;
        background-color: black;
        margin:30px 0 30px 0;

        &--wrapper {
            padding:20px;
        }
    }
    &__item-subdesc {
        font-family: 'Monserrat', sans-serif;
        font-size: 12px;
        color: #fff9;
        margin-left: 30px;
    }
    &__item-desc {
        font-family: 'Monserrat', sans-serif;
        font-size: 18px;
    }
    &__item {
        padding: 5px;
    }
    &__list {
        color: #ffff;
        height:600px;
        overflow:auto;
        display:flex;
        flex-direction:column;
        gap:20px;
    }
    &__radio {
        min-width: 20px;
        min-height: 20px;
        margin:5px 5px 5px 5px;
        cursor:pointer;
        }
    &__cause {
        display:flex;
        align-items:center;
        & > input {
            position: absolute;
            z-index: -1;
            opacity: 0;
            &:not(:disabled):not(:checked) + p:hover:before {
                border-color: #b3d7ff;
            }
            &:not(:disabled):active + p:before {
                background-color: #b3d7ff;
                border-color: #b3d7ff;
            }
            &:focus + p:before  {
                box-shadow: 0 0 0 0.2rem rgba(120, 234, 247, 0.25);
            }
            &:not(:checked):focus + p:before {
                border-color: #80bdff;
            }
            &:checked + p:before {
                border-color: #7CEEFF;
                background-color: #7CEEFF;
                background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%23fff'/%3e%3c/svg%3e");
            }
            &:disabled + p:before {
                background-color: #e9ecef;
            }
        }
        & > p {
            display: inline-flex;
            align-items: center;
            user-select: none;
            &:before {
                content: '';
                display: inline-block;
                width: 1em;
                height: 1em;
                flex-shrink: 0;
                flex-grow: 0;
                border: 1px solid #adb5bd;
                border-radius: 50%;
                margin-right: 0.5em;
                background-repeat: no-repeat;
                background-position: center center;
                background-size: 50% 50%;
            }
        }
    }
    
}



.btn {
    font-family: "Monserrat", sans-serif;
    font-weight: 300;
    font-size: 20px;
    color: black;
    border: none;
    display: inline-block;
    transition: background-color 0.2s linear, color 0.2s linear;
    text-transform: capitalize;
    background-color: #7CEEFF;
    border-radius:20px;
    padding: 10px 20px 10px 20px;
    &:hover {
        background-color:#c9f3fa;
        cursor:pointer;
    }
    &--close {
        margin-top:30px;
    }
    &--next {
        margin-top:30px;
    }
}
.buttons {
    display:flex;
    justify-content:flex-end;
    gap:10px;
    background-color: black;
}
